/* eslint-disable func-names */
/* eslint-disable no-plusplus */

const addFrames = function () {
  const action = document.querySelectorAll('.frame-action');
  let l = 2;
  let i = 2;
  let k = 2;
  action[0].addEventListener('click', (() => {
    // framesArray.push(array);
    const newFrame = document.getElementById('main-frames');
    const div = document.createElement('div');
    div.className = `frame-${i++}`;
    div.id = `${l++}`;
    newFrame.appendChild(div);

    const div1 = document.createElement('div');
    div1.className = 'namber';
    div1.innerHTML = `${k++}`;
    div.appendChild(div1);

    const div2 = document.createElement('div');
    div2.className = 'delete';
    div2.innerHTML = 'X';
    div.appendChild(div2);

    const div3 = document.createElement('div');
    div3.className = 'coppy';
    div3.innerHTML = '<img src="dist/assets/copy.png" alt="18">';
    div.appendChild(div3);

    const div4 = document.createElement('div');
    div4.className = 'move';
    div4.innerHTML = '<img src="dist/assets/move-frame.png" alt="18">';
    div.appendChild(div4);

    const newCanvas = document.createElement('canvas');
    newCanvas.className = 'frame';
    newCanvas.width = 160;
    newCanvas.height = 160;
    const delitCurrent = document.getElementById('current');
    delitCurrent.removeAttribute('id');
    newCanvas.id = 'current';
    div.appendChild(newCanvas);
    // context.clearRect(0, 0, canvas.width, canvas.height);
  }));
};


export default addFrames;
