/* eslint-disable func-names */
/* eslint-disable no-plusplus */

const canvas = {
  canvas: '?',
  array: '?',
};

const choiceCanvas = function () {
  function canvasCreation() {
    const center = document.getElementById('main-canvas');
    const canvasSizeTemp = document.getElementById('canvasSizeTemp');
    let mainCanvas;
    let array;
    let canvasSize;


    function arrayCreator(n) {
      array = new Array(n);
      for (let i = 0; i < n; i++) {
        array[i] = new Array(n);
        for (let j = 0; j < n; j++) {
          array[i][j] = { color: '?' };
        }
      }
    }


    canvasSizeTemp.addEventListener('click', (e) => {
      if (e.target.className === 'choice-canvas') {
        canvasSize = +e.target.innerHTML;
        center.removeChild(canvasSizeTemp);

        mainCanvas = document.getElementById('myCanvas');
        mainCanvas.width = canvasSize * 5;
        mainCanvas.style.width = `${canvasSize * 5}px`;
        mainCanvas.height = canvasSize * 5;
        mainCanvas.className = 'general-canvas';
        center.appendChild(mainCanvas);
        mainCanvas.style.opacity = '1';

        arrayCreator(canvasSize);
        canvas.canvas = mainCanvas;
        canvas.array = array;
        const size = document.getElementById('canvas-size');
        size.innerHTML = ` [${canvas.array.length}x${canvas.array.length}]`;
      }
    });
  }
  canvasCreation();
};

export default choiceCanvas;

export { canvas };
