/* eslint-disable max-len */
/* eslint-disable func-names */
/* eslint-disable no-plusplus */
import { instrument } from './Instruments';
import { canvas } from './ChoiceCanvas';

const paintAllPixelTool = function () {
  const mouse = { x: 0, y: 0 };
  const mainCanvas = document.getElementById('myCanvas');
  canvas.canvas = mainCanvas;
  const context = mainCanvas.getContext('2d');

  mainCanvas.addEventListener('click', (e) => {
    mouse.x = e.pageX - mainCanvas.offsetLeft;
    mouse.y = e.pageY - mainCanvas.offsetTop;
    if (instrument.state === 'bucket-all') {
      const colorToBeChanged = canvas.array[Math.floor(mouse.y / 5)][Math.floor(mouse.x / 5)].color;
      for (let i = 0; i < canvas.array.length; i++) {
        for (let j = 0; j < canvas.array.length; j++) {
          if (canvas.array[i][j].color === colorToBeChanged) {
            canvas.array[i][j].color = instrument.primaryColor;
            context.fillStyle = instrument.primaryColor;
            context.fillRect(
              j * 5, i * 5, 5, 5,
            );
          }
        }
      }
    }
  });
};
export default paintAllPixelTool;
