/* eslint-disable func-names */
/* eslint-disable max-len */
/* eslint-disable no-plusplus */
import { instrument } from './Instruments';
import { canvas } from './ChoiceCanvas';

const horizontalMirrorPenTool = function () {
  const mouse = { x: 0, y: 0 };
  let draw = false;
  const mainCanvas = document.getElementById('myCanvas');
  canvas.canvas = mainCanvas;
  const context = mainCanvas.getContext('2d');


  mainCanvas.addEventListener('mousedown', (e) => {
    mouse.x = e.pageX - mainCanvas.offsetLeft;
    mouse.y = e.pageY - mainCanvas.offsetTop;
    if (instrument.state === 'mirror') {
      draw = true;
      context.fillStyle = instrument.primaryColor;

      for (let i = 0; i < instrument.penSize; i++) {
        for (let j = 0; j < instrument.penSize; j++) {
          canvas.array[Math.floor(mouse.y / 5) + i][Math.floor(mouse.x / 5) + j] = { color: instrument.primaryColor };
        }
      }

      for (let i = 0; i < instrument.penSize; i++) {
        for (let j = 0; j < instrument.penSize; j++) {
          canvas.array[Math.ceil((canvas.array.length * 5 - mouse.y) / 5) - 1 - i][Math.floor(mouse.x / 5) + j] = { color: instrument.primaryColor };
        }
      }


      context.fillRect(
        Math.floor(mouse.x / 5) * 5, Math.floor(mouse.y / 5) * 5, instrument.penSize * 5, instrument.penSize * 5,
      );

      context.fillRect(
        Math.floor(mouse.x / 5) * 5, (canvas.array.length - Math.floor(mouse.y / 5)) * 5 - instrument.penSize * 5, instrument.penSize * 5, instrument.penSize * 5,
      );
    }
  });

  mainCanvas.addEventListener('mousemove', (e) => {
    if (draw === true && instrument.state === 'mirror') {
      mouse.x = e.pageX - mainCanvas.offsetLeft;
      mouse.y = e.pageY - mainCanvas.offsetTop;

      for (let i = 0; i < instrument.penSize; i++) {
        for (let j = 0; j < instrument.penSize; j++) {
          canvas.array[Math.floor(mouse.y / 5) + i][Math.floor(mouse.x / 5) + j] = { color: instrument.primaryColor };
        }
      }

      for (let i = 0; i < instrument.penSize; i++) {
        for (let j = 0; j < instrument.penSize; j++) {
          canvas.array[Math.ceil((canvas.array.length * 5 - mouse.y) / 5) - 1 - i][Math.floor(mouse.x / 5) + j] = { color: instrument.primaryColor };
        }
      }


      context.fillRect(
        Math.floor(mouse.x / 5) * 5, Math.floor(mouse.y / 5) * 5, instrument.penSize * 5, instrument.penSize * 5,
      );

      context.fillRect(
        Math.floor(mouse.x / 5) * 5, (canvas.array.length - Math.floor(mouse.y / 5)) * 5 - instrument.penSize * 5, instrument.penSize * 5, instrument.penSize * 5,
      );
    }
    if (instrument.state === 'mirror') {
      document.onmouseup = () => {
        draw = false;
        document.onmouseup = null;
      };
    }
  });
};

export default horizontalMirrorPenTool;
