/* eslint-disable func-names */
/* eslint-disable no-plusplus */


const instrument = {
  state: '?',
  penSize: 1,
  primaryColor: '#000000',
  secondaryColor: '#000000',
};

const selectionTool = function () {
  const instrumentsConstainerLeft = document.getElementById('left');
  const instrumentsConstainerRight = document.getElementById('right');
  instrumentsConstainerLeft.addEventListener('click', (event) => {
    instrument.state = `${event.target.className}`;
  });
  instrumentsConstainerRight.addEventListener('click', (event) => {
    instrument.state = `${event.target.className}`;
  });
};
const selectionColor = function () {
  const primaryColor = document.getElementById('primary-color');
  const secondaryColor = document.getElementById('secondary-color');
  const swapColor = document.getElementById('swapColor');

  primaryColor.addEventListener('change', () => {
    instrument.primaryColor = primaryColor.value;
  });

  secondaryColor.addEventListener('change', () => {
    instrument.secondaryColor = secondaryColor.value;
  });

  swapColor.addEventListener('click', () => {
    const temp = instrument.primaryColor;
    instrument.primaryColor = instrument.secondaryColor;
    instrument.secondaryColor = temp;
    primaryColor.value = instrument.primaryColor;
    secondaryColor.value = instrument.secondaryColor;
  });
};

const sizeInstrument = document.getElementById('size-palette');
sizeInstrument.addEventListener('click', (e) => {
  if (e.target.className === 'penSize') {
    for (let i = 0; i < 4; i++) {
      sizeInstrument.children[i].className = 'penSize';
      sizeInstrument.children[i].children[0].className = 'square';
    }
    e.target.className = `${e.target.className}Current`;
    e.target.children[0].className = `${e.target.children[0].className}Current`;
    instrument.penSize = +e.target.children[0].innerHTML;
  } else if (e.target.className === 'square') {
    for (let i = 0; i < 4; i++) {
      sizeInstrument.children[i].className = 'penSize';
      sizeInstrument.children[i].children[0].className = 'square';
    }
    e.target.parentNode.className = `${e.target.parentNode.className}Current`;
    e.target.className = `${e.target.className}Current`;
    instrument.penSize = +e.target.innerHTML;
  }
});


export {
  instrument, selectionTool, selectionColor,
};
