/* eslint-disable func-names */
import { instrument } from './Instruments';
import { canvas } from './ChoiceCanvas';

const colorPickerTool = function () {
  const mouse = { x: 0, y: 0 };
  const primaryColor = document.getElementById('primary-color');
  const mainCanvas = document.getElementById('myCanvas');
  mainCanvas.addEventListener('click', (e) => {
    if (instrument.state === 'color-picker') {
      mouse.x = e.pageX - mainCanvas.offsetLeft;
      mouse.y = e.pageY - mainCanvas.offsetTop;
      const temp = canvas.array[Math.floor(mouse.y / 5)][Math.floor(mouse.x / 5)].color;
      instrument.primaryColor = temp;
      primaryColor.value = instrument.primaryColor;
    }
  });
};
export default colorPickerTool;
